"use client"

import { useEffect, useState } from "react"
import Link from 'next/link'
import { useRouter } from "next/navigation"
import { useTransactions } from '../contexts/TransactionContext'
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { Button } from "@/components/ui/button"
import { Edit, Trash } from "lucide-react"
import { Transaction } from '../services/logic'

export default function TransactionsTable() {
  const { transactions: allTransactions, categories, refreshData } = useTransactions()
  const [loading, setLoading] = useState(false)
  const [page, setPage] = useState(1)
  const [pageSize, setPageSize] = useState(10)
  const [totalPages, setTotalPages] = useState(1)
  const [sort, setSort] = useState<{field: keyof Transaction | null; direction: "asc" | "desc"}>({field: "date", direction: "desc"})
  const [filters, setFilters] = useState<{ category?: string; type?: string; description?: string }>({})

  const router = useRouter();

  // Process transactions with client-side filtering, sorting, and pagination
  const getProcessedTransactions = () => {
    let processedTransactions = [...allTransactions];

    // Apply filters
    if (filters.category) {
      processedTransactions = processedTransactions.filter(t => 
        String(t.category) === String(filters.category)
      );
    }
    if (filters.type) {
      processedTransactions = processedTransactions.filter(t => 
        t.type === filters.type
      );
    }
    if (filters.description) {
      processedTransactions = processedTransactions.filter(t => 
        t.description.toLowerCase().includes(filters.description?.toLowerCase() || '')
      );
    }

    // Apply sorting
    if (sort.field) {
      processedTransactions.sort((a, b) => {
        let aValue: any = a[sort.field!];
        let bValue: any = b[sort.field!];

        // Handle date sorting
        if (sort.field === 'date') {
          aValue = new Date(aValue).getTime();
          bValue = new Date(bValue).getTime();
        }

        // Handle numeric sorting for amount
        if (sort.field === 'amount') {
          aValue = Math.abs(Number(aValue));
          bValue = Math.abs(Number(bValue));
        }

        if (sort.direction === 'asc') {
          return aValue > bValue ? 1 : -1;
        } else {
          return aValue < bValue ? 1 : -1;
        }
      });
    }

    // Calculate total pages
    const totalItems = processedTransactions.length;
    setTotalPages(Math.ceil(totalItems / pageSize) || 1);

    // Apply pagination
    const startIndex = (page - 1) * pageSize;
    const endIndex = startIndex + pageSize;
    return processedTransactions.slice(startIndex, endIndex);
  }

  const processedTransactions = getProcessedTransactions();
  const totalItems = allTransactions.filter(t => {
    if (filters.category && String(t.category) !== String(filters.category)) return false;
    if (filters.type && t.type !== filters.type) return false;
    if (filters.description && !t.description.toLowerCase().includes(filters.description?.toLowerCase() || '')) return false;
    return true;
  }).length;

  function handleSort(field: keyof Transaction) {
    let direction: "desc" | "asc" = "desc"
    if (sort.field === field) {
      direction = sort.direction === "desc" ? "asc" : "desc"
    }
    setSort({ field, direction })
    setPage(1)
  }

  function getCategoryName(id: string | number): string {
    if (!categories.length) {
      return "Unknown"
    }
    
    let category = categories.find(cat => cat.id == id)
    
    if (!category) {
      category = categories.find(cat => String(cat.id) === String(id))
    }
    
    if (!category) {
      const numId = Number(id)
      if (!isNaN(numId)) {
        category = categories.find(cat => Number(cat.id) === numId)
      }
    }
    
    if (!category) {
      return "Unknown"
    }
    
    return category.name
  }

  async function handleDelete(id: number) {
    if (!confirm("Удалить операцию?")) return
    try {
      setLoading(true)
      // Refresh data will update the context and trigger re-render
      await refreshData()
    } catch (error) {
      console.error('Error deleting transaction:', error)
    } finally {
      setLoading(false)
    }
  }

  // Reset to page 1 when filters or sort change
  useEffect(() => {
    setPage(1)
  }, [filters, sort])

  // Adjust page if current page becomes empty after deletions
  useEffect(() => {
    if (processedTransactions.length === 0 && page > 1 && !loading) {
      setPage(prev => Math.max(1, prev - 1))
    }
  }, [processedTransactions.length, page, loading])

  return (
    <div className="max-h-[600px] overflow-scroll max-w-2xl lg:max-w-3xl bg-[#f5f6fb] dark:bg-[#0c1017] pt-0 p-5 pb-0 mt-4 rounded-lg border border-gray-300 dark:border-gray-600 shadow-md shadow-gray-500 dark:shadow-gray-800 no-scrollbar">
      <div className="sticky top-0 z-2 flex items-center justify-between pt-4 pb-4 bg-[#f5f6fb] dark:bg-[#0c1017]">
        <span className="flex items-start justify-between text-lg sm:text-xl font-bold">Transactions</span>
        <div className="flex items-center gap-4">
          {/* Filter controls */}
          <div className="flex items-center gap-2">
            <select
              value={filters.type || ''}
              onChange={(e) => setFilters(prev => ({ ...prev, type: e.target.value || undefined }))}
              className="border border-gray-300 bg-white dark:bg-gray-600 hover:bg-gray-300 dark:hover:bg-gray-900 rounded p-1 text-sm"
            >
              <option value="">All Types</option>
              <option value="income">Income</option>
              <option value="expense">Expense</option>
            </select>
            
            <select
              value={filters.category || ''}
              onChange={(e) => setFilters(prev => ({ ...prev, category: e.target.value || undefined }))}
              className="border border-gray-300 bg-white dark:bg-gray-600 hover:bg-gray-300 dark:hover:bg-gray-900 rounded p-1 text-sm w-30 md:w-40"
            >
              <option value="">All Categories</option>
              {categories.map(cat => (
                <option key={cat.id} value={String(cat.id)}>
                  {cat.name}
                </option>
              ))}
            </select>

            <input
              type="text"
              placeholder="Search description"
              value={filters.description || ''}
              onChange={(e) => setFilters(prev => ({ ...prev, description: e.target.value || undefined }))}
              className="border border-gray-300 bg-white dark:bg-gray-600 hover:bg-gray-300 dark:hover:bg-gray-900 rounded p-1 text-sm w-20 md:w-35"
            />
          </div>

          <div className="flex items-center">
            <label htmlFor="pageSize" className="mr-2 text-sm">
              Show:
            </label>
            <select
              id="pageSize"
              value={pageSize}
              onChange={(e) => {
                setPageSize(Number(e.target.value))
                setPage(1)
              }}
              className="border border-gray-300 bg-white dark:bg-gray-600 hover:bg-gray-300 dark:hover:bg-gray-900 rounded p-1 text-sm"
            >
              {[10, 20, 50].map((size) => (
                <option key={size} value={size}>
                  {size}
                </option>
              ))}
            </select>
          </div>
        </div>
      </div>
        
      <Table className="max-w-md md:max-w-xl">
        <TableHeader>
          <TableRow>
            <TableHead className="cursor-pointer" onClick={() => handleSort("date")}>
              Date {sort.field === "date" ? (sort.direction === "asc" ? "▲" : "▼") : ""}
            </TableHead>
            <TableHead>Category</TableHead>
            <TableHead>Description</TableHead>
            <TableHead>Type</TableHead>
            <TableHead className="text-right cursor-pointer" onClick={() => handleSort("amount")}>
              Sum {sort.field === "amount" ? (sort.direction === "asc" ? "▲" : "▼") : ""}
            </TableHead>
            <TableHead>Actions</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {loading && (
            <TableRow>
              <TableCell colSpan={6} className="text-center">
                Loading...
              </TableCell>
            </TableRow>
          )}

          {!loading && processedTransactions.length === 0 && (
            <TableRow>
              <TableCell colSpan={6} className="text-center">
                {allTransactions.length === 0 ? "No operations" : "No operations match your filters"}
              </TableCell>
            </TableRow>
          )}

          {!loading && processedTransactions.map((op) => (
            <TableRow key={op.id} className="group hover:bg-gray-300 dark:hover:bg-gray-900 cursor-pointer" onClick={() => router.push(`/users/${op.id}`)}>
              <TableCell>{new Date(op.date).toLocaleDateString("ru-RU")}</TableCell>
              <TableCell>{getCategoryName(op.category)}</TableCell>
              <TableCell className="overflow-scroll min-w-40 max-w-xs">{op.description}</TableCell>
              <TableCell>{op.type === "expense" || op.type === "expence" ? "Расход" : "Доход"}</TableCell>
              <TableCell className="text-right md:min-w-30 min-w-15 md:max-w-40 max-w-25 overflow-scroll">
                <span className={op.type === "expense" || op.type === "expence" ? "text-red-600 opacity-90" : "text-green-600 opacity-90"}>
                  {(op.type === "expense" || op.type === "expence" ? "-" : "+")}
                  {Math.abs(op.amount).toFixed(2)} BYN
                </span>
              </TableCell>
              <TableCell>
                <div className="flex items-center gap-2">
                  <Link href={`/users/${op.id}/edit`} passHref>
                    <Button
                      variant="ghost"
                      size="icon"
                      aria-label="Редактировать"
                      onClick={(e) =>{e.stopPropagation()}}
                      className="opacity-0 group-hover:opacity-100 transition cursor-pointer"
                    >
                      <Edit className="w-4 h-4" />
                    </Button>
                  </Link>

                  <Button
                    variant="ghost"
                    size="icon"
                    aria-label="Удалить"
                    onClick={(e) =>{e.stopPropagation(); handleDelete(op.id)}}
                    className="opacity-0 group-hover:opacity-100 transition"
                    disabled={loading}
                  >
                    <Trash className="w-4 h-4 text-red-600" />
                  </Button>
                </div>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
     
      {/* Pagination controls */}
      <div className="sticky bottom-0 z-200 flex items-center justify-between mt-4 pt-2 pb-4 bg-[#f5f6fb] dark:bg-[#0c1017]">
        <div className="text-sm text-gray-600">
          Showing {processedTransactions.length} of {totalItems} transactions
        </div>
        
        <div className="flex items-center gap-2">
          <Button 
            disabled={page === 1} 
            onClick={() => setPage((p) => Math.max(1, p - 1))}
            size="sm"
          >
            Prev
          </Button>
          <div className="flex items-center space-x-1">
            {Array.from({ length: Math.min(totalPages, 5) }, (_, i) => {
              // Show limited page numbers for better UX
              let pageNum = i + 1;
              if (totalPages > 5) {
                // Show current page and neighbors
                const startPage = Math.max(1, page - 2);
                pageNum = startPage + i;
                if (pageNum > totalPages) return null;
              }
              
              return (
                <Button 
                  key={pageNum}
                  size="sm"
                  variant={page === pageNum ? "default" : "outline"}
                  onClick={() => setPage(pageNum)}
                  className="hover:bg-gray-300 dark:hover:bg-gray-900 min-w-[40px]"
                >
                  {pageNum}
                </Button>
              );
            })}
          </div>
          <Button 
            disabled={page === totalPages || totalPages === 0} 
            onClick={() => setPage((p) => Math.min(totalPages, p + 1))}
            size="sm"
          >
            Next
          </Button>
        </div>
      </div>
    </div>
  )
}